.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.ChatBuyItemBox__header {
  display: flex;
  justify-content: flex-start;
  height: 82px;
  padding: 16px 12px;
  border-bottom: 1px solid #ebebeb;
  cursor: pointer;
  position: relative; }
  .ChatBuyItemBox__header .leftBox {
    display: inline-block;
    position: relative;
    margin-right: 12px; }
  .ChatBuyItemBox__header .headbox {
    width: 50px;
    height: 50px;
    position: relative;
    border-radius: 50%; }
    .ChatBuyItemBox__header .headbox .headshadBox {
      position: absolute;
      left: 0;
      top: 0;
      border: solid 1px rgba(255, 255, 255, 0.5);
      border-radius: 50%;
      width: 100%;
      height: 100%;
      z-index: 1; }
    .ChatBuyItemBox__header .headbox .userHead {
      width: 100%;
      height: 100%; }
  .ChatBuyItemBox__header .headMsgNum {
    top: 0;
    right: 0;
    z-index: 2; }
  .ChatBuyItemBox__header .cont {
    height: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 215px; }
  .ChatBuyItemBox__header .userName {
    font-size: 16px;
    font-weight: 500;
    color: #001a33;
    margin-right: auto;
    margin-bottom: 8px; }

.ChatBuyItemBox__content {
  background: #f7f7f7; }
  .ChatBuyItemBox__content .orderCarItem {
    padding: 12px 12px 8px;
    display: flex;
    height: 116px;
    position: relative;
    overflow: hidden; }
    .ChatBuyItemBox__content .orderCarItem:hover {
      background: #f0feff; }
    .ChatBuyItemBox__content .orderCarItem:not(:last-of-type) {
      border-bottom: 1px solid #ebebeb; }
    .ChatBuyItemBox__content .orderCarItem .carImgBox {
      flex-grow: 0;
      flex-shrink: 0;
      width: 80px;
      height: 60px;
      margin-right: 10px;
      border: 1px solid #ebebeb;
      display: flex;
      justify-content: center;
      display: flex;
      align-items: center; }
      .ChatBuyItemBox__content .orderCarItem .carImgBox .carImg {
        object-fit: cover;
        width: 100%;
        height: 100%; }
    .ChatBuyItemBox__content .orderCarItem .orderCarinfo {
      width: 150px;
      flex: auto; }
      .ChatBuyItemBox__content .orderCarItem .orderCarinfo .carName {
        color: #001a33;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin-bottom: 5px;
        font-family: SFBoldText; }
      .ChatBuyItemBox__content .orderCarItem .orderCarinfo .milesAtag {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
        margin-right: -20px; }
        .ChatBuyItemBox__content .orderCarItem .orderCarinfo .milesAtag .mileage {
          font-family: SFProText;
          margin-right: 8px;
          font-size: 14px; }
      .ChatBuyItemBox__content .orderCarItem .orderCarinfo .carPrice {
        color: #8a9199;
        font-size: 12px;
        margin-bottom: 5px;
        font-family: SFProText; }

.orderTag {
  font-size: 12px;
  height: 18px;
  line-height: 16px;
  border: 1px solid #3acdfe;
  border-radius: 2px;
  color: #3acdfe;
  padding: 0 5px;
  display: inline-block;
  font-weight: 600;
  font-family: SFProText; }

.orderMsgNum {
  left: 8px;
  top: 8px; }

.location {
  width: 19px;
  height: 14px;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2; }

.headLastMsg {
  width: 234px;
  display: flex;
  align-items: center;
  margin-bottom: 0;
  font-family: SFProText; }

.lastMsgCon {
  color: #001a33;
  font-size: 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.conLastMsg {
  width: 206px;
  display: flex;
  align-items: center;
  margin-bottom: 0;
  font-family: SFProText; }
