.message_car {
  width: 750px;
  height: 144px;
  background-color: #fff;
  padding: 12px;
  display: flex;
  justify-content: flex-start;
  margin: 20px auto; }
  .message_car .imageArea {
    width: 160px;
    height: 120px;
    position: relative;
    margin-right: 12px; }
    .message_car .imageArea .cover {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    .message_car .imageArea .makeLogo {
      position: absolute;
      left: 0;
      top: 0;
      width: 34px;
      height: 34px; }
  .message_car .infoArea {
    width: 554px;
    height: 120px;
    position: relative; }
    .message_car .infoArea .carName {
      margin-bottom: 8px;
      height: 27px;
      font-family: SFProText;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 554px;
      font-size: 18px;
      font-weight: 500;
      color: #00274d; }
    .message_car .infoArea .carStatus {
      display: flex;
      align-items: center;
      width: 100%;
      height: 22px;
      margin-bottom: 12px; }
      .message_car .infoArea .carStatus .carUnit {
        display: flex;
        justify-content: center;
        display: flex;
        align-items: center;
        font-family: SFBoldText;
        height: 22px;
        padding: 4px 8px;
        border-radius: 4px;
        background-color: #3acdfe;
        font-size: 12px;
        text-align: center;
        color: #ffffff; }
        .message_car .infoArea .carStatus .carUnit .number {
          margin-right: 4px; }
    .message_car .infoArea .carMileage {
      font-family: SFProText;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 554px;
      width: 100%;
      height: 21px;
      font-size: 14px;
      color: #8a9199; }
      .message_car .infoArea .carMileage i {
        font-size: 16px;
        margin-right: 8px;
        color: #198cff;
        transform: translateY(1px); }
        .message_car .infoArea .carMileage i ~ span {
          height: 21px;
          font-family: SFProText;
          font-size: 14px;
          color: #8a9199; }

.bubble {
  font-family: SFProText;
  display: inline-flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  padding: 12px 16px;
  line-height: 1.2;
  border-radius: 4px;
  background-color: #3acdfe;
  font-size: 16px;
  color: #ffffff;
  position: relative;
  min-height: 48px;
  word-break: break-word; }
  .bubble_left {
    color: #ffffff; }
    .bubble_left:before {
      content: '';
      position: absolute;
      right: -6px;
      top: 12px;
      width: 0px;
      height: 0px;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
      border-left: 6px solid #3acdfe; }
  .bubble_right {
    border: solid 1px #ebebeb;
    background-color: #ffffff;
    color: #00274d; }
    .bubble_right:before, .bubble_right:after {
      content: '\0020';
      display: block;
      position: absolute;
      top: 12px;
      left: -6px;
      z-index: 2;
      width: 0;
      height: 0;
      overflow: hidden;
      border: solid 6px transparent;
      border-left: 0;
      border-right-color: #fff; }
    .bubble_right:before {
      left: -7px;
      z-index: 1;
      border-right-color: #ebebeb;
      transform: scale(1.1); }
  .bubble_right.bubble_blue {
    background-color: #3acdfe;
    color: #fff;
    border: none; }
    .bubble_right.bubble_blue:after {
      display: none; }
    .bubble_right.bubble_blue:before {
      content: '';
      left: -6px;
      z-index: 1;
      border-right: 6px solid #3acdfe; }

.message_text {
  display: flex;
  justify-content: flex-end;
  padding: 0 20px;
  width: 100%;
  position: relative;
  margin: 20px 0; }
  .message_text .user {
    width: 50px;
    height: 50px;
    position: relative;
    flex-shrink: 0; }
    .message_text .user .headshot {
      width: 50px;
      height: 50px;
      border-radius: 50%; }
    .message_text .user .location {
      width: 19px;
      height: 14px;
      position: absolute;
      right: 0;
      bottom: 0; }
  .message_text .contentWrap {
    position: relative;
    max-width: 340px;
    height: auto; }
  .message_text .message_status {
    width: 24px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #f2565f; }
    .message_text .message_status img,
    .message_text .message_status svg {
      width: 24px;
      height: 24px; }
  .message_text .resend {
    cursor: pointer; }

.text_left {
  justify-content: flex-end; }
  .text_left .user {
    margin-left: 16px;
    margin-right: 0;
    order: 3; }
  .text_left .contentWrap {
    order: 2;
    display: flex;
    justify-content: flex-end; }
  .text_left .message_status {
    margin-right: 16px;
    order: 1; }

.text_right {
  justify-content: flex-start; }
  .text_right .user {
    margin-left: 0;
    margin-right: 16px;
    order: 1; }
  .text_right .contentWrap {
    order: 2; }
  .text_right .message_status {
    margin-left: 16px;
    order: 3; }

.message_image {
  width: 100%;
  height: auto;
  max-width: 100%;
  max-height: 255px;
  position: relative;
  margin: 0 auto;
  border-radius: 8px;
  border: solid 1px #ebebeb;
  object-fit: cover;
  cursor: pointer; }

.image_loading, .image_error {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 340px;
  max-height: 255px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center; }
  .image_loading img, .image_error img {
    width: 24px;
    height: 24px; }

.error_wrap {
  width: 104px;
  height: 64px;
  border-radius: 4px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center; }
  .error_wrap i,
  .error_wrap svg {
    color: red;
    width: 22px;
    height: 22px; }

.message_offer_success,
.message_offer_revoke {
