  top: -12px;
  left: calc(50% - 16px);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #00274d;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2px; }

.message_text_max .contentWrap {
  max-width: 360px; }
