@font-face {
  font-family: SFProText;
  src: url("../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.fold {
  font-size: 16px; }

.location {
  width: 19px;
  height: 14px;
  position: absolute;
  bottom: 0;
  left: 30px;
  z-index: 2; }

.lastMsgIcon {
  margin-right: 4px;
  font-size: 14px; }

.msgTime {
  font-size: 12px;
  color: #8a9199;
  padding-top: 3px;
  height: 18px;
  margin-left: auto; }

.show {
  display: block; }

.hide {
  display: none; }

.activeOrder {
  background: #f0feff; }

.noMsg {
  padding: 120px 0 0 0;
  text-align: center; }
  .noMsg .noMsgCon {
    font-size: 14px;
    line-height: 1.5;
    color: #8a9199; }

.mileage_icon {
  font-size: 16px;
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  color: #198cff !important;
  margin-right: 4px;
  padding-bottom: 1px; }

.showMoreOrder {
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  height: 50px;
  font-size: 16px;
  color: #5c6166; }
  .showMoreOrder > span {
    cursor: pointer; }
