.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.ChatSellItemBox__header {
  display: flex;
  justify-content: flex-start;
  height: 86px;
  padding: 12px;
  border-bottom: 1px solid #ebebeb;
  position: relative;
  cursor: pointer; }
  .ChatSellItemBox__header .carImgBox {
    width: 80px;
    height: 60px;
    flex-grow: 0;
    flex-shrink: 0;
    margin-right: 10px;
    border: 1px solid #ebebeb;
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    position: relative; }
    .ChatSellItemBox__header .carImgBox .carImg {
      object-fit: cover;
      width: 100%;
      height: 100%; }
    .ChatSellItemBox__header .carImgBox .orderMsgNum {
      right: -5px;
      top: -7px; }
  .ChatSellItemBox__header .orderCarinfo .carName {
    font-size: 14px;
    font-weight: 500;
    color: #001a33;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: normal;
    margin-bottom: 5px; }
  .ChatSellItemBox__header .orderCarinfo .milesAtag {
    display: flex;
    align-items: center;
    margin-bottom: 5px; }
    .ChatSellItemBox__header .orderCarinfo .milesAtag .mileage {
      font-family: SFProText;
      margin-right: 8px;
      font-size: 14px;
      color: #8a9199; }

.ChatSellItemBox__content {
  background: #f7f7f7; }
  .ChatSellItemBox__content .orderSellItem {
    height: 84px;
    padding: 12px 12px 5px;
    display: flex;
    position: relative; }
    .ChatSellItemBox__content .orderSellItem:hover {
      background: #ebebeb; }
    .ChatSellItemBox__content .orderSellItem:not(:last-of-type) {
      border-bottom: 1px solid #ebebeb; }
    .ChatSellItemBox__content .orderSellItem .leftBox {
      position: relative;
      margin-right: 12px;
      width: 48px;
      height: 48px; }
    .ChatSellItemBox__content .orderSellItem .headbox {
      width: 100%;
      height: 100%;
      position: relative;
      border-radius: 50%; }
      .ChatSellItemBox__content .orderSellItem .headbox .headshadBox {
        position: absolute;
        left: 0;
        top: 0;
        border: solid 1px rgba(255, 255, 255, 0.5);
        border-radius: 50%;
        width: 100%;
        height: 100%;
        z-index: 1; }
      .ChatSellItemBox__content .orderSellItem .headbox .userHead {
        width: 100%;
        height: 100%; }
    .ChatSellItemBox__content .orderSellItem .headMsgNum {
      top: 0;
      z-index: 2; }
    .ChatSellItemBox__content .orderSellItem .orderInfo {
      width: 180px;
      flex: auto; }
      .ChatSellItemBox__content .orderSellItem .orderInfo .userName {
        margin-bottom: 2px;
        font-size: 14px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.5;
        letter-spacing: normal;
        color: #001a33;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        max-width: 180px; }
      .ChatSellItemBox__content .orderSellItem .orderInfo .offer {
        font-size: 12px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 1.5;
        letter-spacing: normal;
        color: #8a9199;
        margin-bottom: 6px;
        height: 18px; }

.fold {
  position: absolute;
  right: 10px;
  bottom: 10px; }

.lastMsg {
  width: 236px;
  display: flex;
  align-items: center;
  margin-bottom: 0;
  font-family: SFProText; }
  .lastMsg .lastMsgCon {
    color: #001a33;
    font-size: 12px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; }
