@font-face {
  font-family: SFProText;
  src: url("../../../assets/fonts/sf-pro-text_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBigText;
  src: url("../../../assets/fonts/sf-pro-display_regular.woff2") format("woff2"); }

@font-face {
  font-family: SFBoldText;
  src: url("../../../assets/fonts/sf-pro-text_semibold.woff2") format("woff2"); }

@font-face {
  font-family: DIN;
  src: url("../../../assets/fonts/dinweb-medium-woff-data.woff") format("woff"); }

.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.ChatBox {
  width: 320px;
  position: fixed;
  right: 40px;
  bottom: 0;
  z-index: 99;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.12); }
  .ChatBox__Header {
    display: flex;
    align-items: center;
    background-color: #001a33;
    height: 58px;
    padding: 12px 12px;
    color: #ffffff;
    position: relative; }
    .ChatBox__Header .userHead {
      margin-right: 12px;
      border: solid 1px rgba(255, 255, 255, 0.5); }
    .ChatBox__Header .userName {
      margin-right: auto; }
    .ChatBox__Header .fullscreen {
      cursor: pointer;
      font-size: 20px;
      margin-right: 12px;
      align-self: flex-start; }
    .ChatBox__Header .cancel {
      align-self: flex-start;
      cursor: pointer;
      font-size: 20px; }
    .ChatBox__Header .location {
      position: absolute;
      width: 16px;
      left: 28px;
      top: 32px;
      z-index: 1; }
  .ChatBox__title {
    background: #ffffff;
    display: flex;
    align-items: center;
    height: 50px;
    border-bottom: 1px solid #ebebeb; }
    .ChatBox__title__item {
      display: flex;
      justify-content: center;
      display: flex;
      align-items: center;
      height: 100%;
      width: 50%;
      text-align: center;
      color: #001a33;
      font-size: 16px;
      border-bottom: 2px solid #ffffff; }
      .ChatBox__title__item .box {
        cursor: pointer;
        display: flex;
        align-items: center;
        height: 100%;
        position: relative;
        padding: 0 11px; }
        .ChatBox__title__item .box .MenuItem {
          font-family: SFProText;
          font-weight: 500;
          font-size: 16px; }
    .ChatBox__title .itemPre {
      font-size: 24px;
      margin-right: 5px; }
    .ChatBox__title .titleMsgNum {
      right: 0px;
      top: 4px; }
  .ChatBox__Content {
    height: 532px;
    background: #ffffff;
    position: relative; }

.menuItemActive {
  color: #3bcdfe;
  border-bottom: 2px solid #3bcdfe; }

.msgBox {
  position: relative; }

.chatMinBox {
  width: 280px;
  height: 58px;
  position: fixed;
  right: 40px;
  bottom: 0;
  z-index: 1;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.12);
  padding: 16px 16px 16px 18px;
  border-radius: 4px;
  background-color: #00274d;
  color: #ffffff;
  display: flex;
  align-items: center; }
  .chatMinBox .commentHintBox {
    margin-right: 14px;
    color: #3acdfe;
    position: relative; }
    .chatMinBox .commentHintBox .comment {
      font-size: 28px; }
    .chatMinBox .commentHintBox .TrackPoint {
      position: absolute;
      right: -5px;
      top: -5px;
      width: 10px;
      height: 10px;
      background: #f2565f;
      border-radius: 50%; }
  .chatMinBox .allMsgNum {
    font-size: 16px;
    margin-right: auto; }
  .chatMinBox .fullscreen {
    font-size: 20px;
    margin-top: -5px;
    margin-left: auto; }

.hide {
  display: none; }
