.ant-notification {
  width: 298px !important; }

.ant-notification-notice {
  padding: 12px !important; }

.ant-notification-notice-message {
  margin-bottom: 0 !important;
  padding-right: 0px !important; }

.ant-notification-notice-close {
  margin-top: -5px !important;
  top: 6px !important;
  right: 12px !important; }
  .ant-notification-notice-close svg {
    width: 10px !important;
    height: 10px !important;
    color: #001a33 !important; }

.modal {
  display: flex;
  justify-content: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
  width: 480px;
  height: 209px;
  border-radius: 8px;
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.06);
  background-color: #ffffff; }
  .modal .cancelIcon {
    display: flex;
    justify-content: center;
    display: flex;
    align-items: center;
    position: absolute;
    top: 12px;
    right: 12px;
    width: 16px;
    height: 16px;
    color: #001a33;
    cursor: pointer; }
  .modal .content {
    font-family: SFProText;
    color: #001a33;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    width: 100%;
    height: 128px;
    padding: 40px 24px; }
  .modal .divider {
    width: 100%;
    height: 1px;
    background-color: #ebebeb; }
  .modal .operation {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 100%;
    height: 80px;
    padding: 20px 24px; }
    .modal .operation .button {
      display: flex;
      justify-content: center;
      display: flex;
      align-items: center;
      font-family: SFBoldText;
      color: #ffffff;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      letter-spacing: normal;
      width: 118px;
      height: 40px;
      border: 0px;
      border-radius: 4px;
      padding: 0px;
      background-color: #3acdfe; }
